import React, { useState, useEffect } from 'react';
import axios from "axios";
import { FaEdit } from "react-icons/fa";
import Header from '../components/Header.js';
import PlusButton from '../components/PlusButton.js';
import ProductCard from '../components/ProductCard.js';
import ProductForm from '../components/ProductForm.js';
import Suggestions from '../components/Suggestions.js';
import { useNavigate } from 'react-router-dom';

const API_URL = 'http://localhost:8000/api/products';

function HomePage() {
  const [cards, setCards] = useState([]);
  const [showForm, setShowForm] = useState(false);
  const [error, setError] = useState("");
  const [expandedCard, setExpandedCard] = useState(null);
  const [showSuggestions, setShowSuggestions] = useState(false);
  const navigate = useNavigate();
  const isLoggedIn = localStorage.getItem('isAdminLoggedIn') === 'true';

  useEffect(() => {
    fetch(API_URL)
      .then((res) => res.json())
      .then((data) => setCards(data))
      .catch(() => setError("Failed to fetch products."));
  }, []);

  const addCard = async (product) => {
    setError("");
    try {
      const res = await fetch(API_URL, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          name: product.name,
          productId: product.productId,
          image: product.image,
          stock: Number(product.stock),
          manufactureDate: product.manufactureDate,
          expiryDate: product.expiryDate,
          normalDate: product.normalDate
        })
      });
      if (res.ok) {
        const added = await res.json();
        setCards([...cards, added]);
        setShowForm(false);
      } else {
        setError("Failed to add product.");
      }
    } catch {
      setError("Failed to add product.");
    }
  };

  const toggleForm = () => {
    setShowForm(!showForm);
  };

  return (
    <div className="flex flex-col min-h-screen bg-gray-50">
      <Header />
      <div className="flex-grow container mx-auto px-4 py-8">
        {showForm && isLoggedIn && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
            <ProductForm onSubmit={addCard} onCancel={toggleForm} />
          </div>
        )}
        {showSuggestions && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
            <div className="bg-white rounded-lg shadow-lg max-w-lg w-full">
              <Suggestions />
              <div className="flex justify-end p-4">
                <button
                  className="bg-gray-300 text-gray-700 px-4 py-2 rounded-md hover:bg-gray-400 transition"
                  onClick={() => setShowSuggestions(false)}
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        )}
        {error && <div className="text-red-500 mb-4">{error}</div>}
        {isLoggedIn ? (
          <>
            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-1 mt-6">
              {cards.map((card, idx) => (
                <ProductCard
                  key={card.id}
                  image={card.image}
                  name={card.name}
                  productId={card.productId}
                  stock={card.stock}
                  isExpanded={expandedCard === idx}
                  onToggle={() => setExpandedCard(expandedCard === idx ? null : idx)}
                />
              ))}
            </div>

            {/* Centered action buttons */}
            <div className="flex justify-center mt-10 space-x-6">
              <button
                className="bg-blue-600 text-white px-6 py-3 rounded-full shadow hover:bg-blue-700 transition-colors text-lg"
                onClick={() => navigate('/admin/products')}
              >
                Edit
              </button>
              <button
                className="bg-green-600 text-white px-6 py-3 rounded-full shadow hover:bg-green-700 transition-colors text-lg"
                onClick={toggleForm}
              >
                Add
              </button>
              <button
                className="bg-yellow-500 text-white px-6 py-3 rounded-full shadow hover:bg-yellow-600 transition-colors text-lg"
                onClick={() => setShowSuggestions(true)}
              >
                Suggestions
              </button>
              <button
                className="bg-purple-600 text-white px-6 py-3 rounded-full shadow hover:bg-purple-700 transition-colors text-lg"
                onClick={() => navigate('/dashboard')}
              >
                Dashboard
              </button>
            </div>
          </>
        ) : (
          <div className="text-center text-gray-500 mt-10 text-xl font-semibold">
            Please log in as admin to view and manage products.
          </div>
        )}
      </div>
    </div>
  );
}

export default HomePage;