import React, { useEffect, useState } from 'react';
import ReactMarkdown from 'react-markdown';

function Suggestions() {
  const [status, setStatus] = useState('analyzing');
  const [result, setResult] = useState('');
  const [expanded, setExpanded] = useState(false);

  useEffect(() => {
    let interval;
    const fetchSuggestions = async () => {
      try {
        const res = await fetch('http://localhost:8000/api/products/suggestions');
        const data = await res.json();
        setStatus(data.status);
        setResult(data.result);
        if (data.status === 'done') {
          setExpanded(true);
        } else {
          setExpanded(false);
        }
        if (data.status === 'analyzing') {
          interval = setTimeout(fetchSuggestions, 2000);
        }
      } catch (e) {
        setStatus('error');
        setResult('Failed to fetch suggestions.');
      }
    };
    fetchSuggestions();
    return () => clearTimeout(interval);
  }, []);

  return (
    <div
      className={`transition-all duration-500 bg-white rounded-lg shadow-lg mx-auto ${
        expanded ? 'max-w-3xl min-h-[400px] max-h-[600px] p-8 overflow-y-auto' : 'max-w-md min-h-[180px] p-6'
      } flex flex-col justify-center`}
      style={{ overflowY: expanded ? 'auto' : 'hidden' }}
    >
      <h2 className="text-2xl font-bold mb-4 text-center">Suggestions</h2>
      {status === 'analyzing' ? (
        <p className="text-yellow-600 font-semibold text-center">Analyzing your products with Llama3... Please wait.</p>
      ) : status === 'error' ? (
        <p className="text-red-500 text-center">{result}</p>
      ) : (
        <div className="text-gray-700 whitespace-pre-line mb-4 prose max-w-none" style={{overflowY: 'auto', maxHeight: 400}}>
          <ReactMarkdown>{result}</ReactMarkdown>
        </div>
      )}
    </div>
  );
}

export default Suggestions;
